

















































import { Component, Vue, Watch } from 'vue-property-decorator';
import UiIntro from '@/components/UiIntro.vue';

declare global {
    interface Window {
        ble: any;
        cordova: any;
        plugins: any;
        StatusBar: any;
    }
}
@Component({
    components: { UiIntro }
})
export default class App extends Vue {
    allDevices: Array<any> = [];
    distance = 0;
    isBackground = false;
    timestamp = Date.now();
    hasLocalIntro = false;
    hasAlert = false;

    created() {
        this.getStoredDevices();
        this.getStoredDistance();
        document.addEventListener('deviceready', this.onDeviceReady, false);
    }

    onDeviceReady() {
        window.cordova.plugins.backgroundMode.enable();
        this.startBackgroundNotifications();

        setInterval(() => {
            this.collectDevices();
        }, 700);

        setInterval(() => {
            this.cleanAlerts();
        }, 5000);
    }

    reset() {
        this.allDevices = [];
        return localStorage.removeItem('INTRO');
    }

    get hasIntro() {
        return localStorage.getItem('INTRO') || this.hasLocalIntro;
    }

    closeIntro() {
        this.hasLocalIntro = true;
        localStorage.setItem('INTRO', JSON.stringify(true));
    }

    getStoredDistance() {
        const distance = localStorage.getItem('DISTANCE') || '0';
        this.distance = JSON.parse(distance);
    }

    getStoredDevices() {
        const devices = localStorage.getItem('DEVICES') || '[]';
        this.allDevices = JSON.parse(devices);
    }

    startBackgroundNotifications() {
        window.cordova.plugins.backgroundMode.on('activate', () => {
            this.isBackground = true;
        });
        window.cordova.plugins.backgroundMode.on('deactivate', () => {
            this.isBackground = false;
            window.cordova.plugins.notification.badge.clear();
        });
    }

    sendPushNotification() {
        window.cordova.plugins.notification.local.schedule({
            title: 'ALERT',
            text: 'Someone is too close'
        });
    }

    cleanAlerts() {
        this.allDevices.forEach(d => {
            d.isDanger = false;
        });
    }

    collectDevices() {
        window.ble.startScan([], this.onDiscoverDevice, this.onError);
    }

    onDiscoverDevice(d: any) {
        this.hasAlert = false;
        if (!this.hasIntro) return;
        const match = this.allDevices.find(e => e.id === d.id);
        console.log('run');

        if (!match) {
            this.allDevices.push(d);
        } else {
            match.rssi = d.rssi;
            match.distance = d.rssi * -2.5;
            match.isDanger = this.shouldAlert(d.rssi);

            if (match.isDanger && !match.excluded) {
                this.hasAlert = true;
                navigator.vibrate(200);
                // if (this.isBackground) {
                //     console.log('re');
                //     match.hasNotification = true;
                //     this.sendPushNotification();
                // }
            }
        }
    }

    shouldAlert(rssi: number) {
        let distance = -60;
        if (this.distance === 1) {
            distance = -80;
        }
        if (this.distance === 2) {
            distance = -90;
        }
        if (this.distance === 3) {
            distance = -100;
        }
        return rssi > distance;
    }

    onError(e: any) {
        console.log('Error trying to get device', e);
    }

    @Watch('allDevices')
    onDevicesChange(d: any) {
        localStorage.setItem('DEVICES', JSON.stringify(d));
        this.timestamp = Date.now();
    }

    changeDistance(distance: number) {
        this.distance = distance;
        localStorage.setItem('DISTANCE', JSON.stringify(distance));
    }
}
